
document.addEventListener('DOMContentLoaded', function() {
    activateListeners();
    
});
// activa todos los listeners
function activateListeners(){
    menuHover();
    actualziarLista();
}
// Hover del menu despegable
function menuHover(){
    const portafolio = document.querySelector(".portafolio");
    const menu = document.querySelector(".overlayMenu");
    let timeOut;
    portafolio.addEventListener('mouseenter', function() {
        clearTimeout(timeOut);
        menu.style.height = "15rem";
        console.log('entrando');
    });
    portafolio.addEventListener('mouseleave', function() {
        timeOut = setTimeout(function() {
            menu.style.height = "0rem";
        }, 200);
        console.log('saliendo');
    });
    menu.addEventListener('mouseenter', function() {
        clearTimeout(timeOut);
    })
    menu.addEventListener('mouseleave', function() {
        timeOut = setTimeout(function() {
            menu.style.height = "0rem";
        }, 200);
        console.log('saliendo');
    })
}
// Listener del boton de actualziar lista
function actualziarLista(){
    document.getElementById('seccion-contenido').addEventListener('click', function(event) {
        if (event.target.classList.contains('btn-actualizar')) {
          const mensajeValue = event.target.value;
          window.location.href = `listas.php?mensaje=${mensajeValue}`;
        }
      });
}
//listener overlayImagenFull
function overlayImagenFull(id){
    const imagenGrande = document.querySelector('.imagenGrande');
    const imagenOverlay = document.querySelector('.imagen-overlay');
    const body = document.querySelector('.bodyLock');
    if(id == "none"){
        imagenGrande.style.display = "none";
        body.style.overflow = "unset";
    }else {
        imagenGrande.style.display = "flex";
        imagenOverlay.src = id;
        body.style.overflow = "hidden";
    }
}




